let languages = {
  'en': 'English',
  'de': 'Deutsch',
  'es': 'Espaol',
  'fr': 'Franais',
  'it': 'Italiano',
  'pl': 'Polskie',
  'ru': '',
  'zh-cn': '',
  'kr': ''
}

let current = localStorage.getItem('lang')
current = current === null || !(current in languages) ? 'en' : current

export default {
  languages,
  current
}



